<!--  -->
<template>
  <div class="box">
      <div id="left"></div>
      <div id="right"></div>
  </div>
</template>

<script>
import echarts from "echarts";

export default {
  mounted() {
      var lmyChart = echarts.init(document.getElementById('left'));
      var rmyChart = echarts.init(document.getElementById('right'));

        // 指定图表的配置项和数据
        var leftOption = {
            title: {
                text: 'ECharts 入门示例'
            },
            tooltip: {},
            legend: {
                data:['销量']
            },
            grid: {
              //坐标系（）
              right: "0"
            },
            xAxis: {
                data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子","衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子","衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'line',
                data: [5, 20, 36, 10, 10, 20,5, 20, 36, 10, 10, 20,5, 20, 36, 10, 10, 20]
            }]
        };
        var rightOption = {
            title: {
                text: 'ECharts 入门示例'
            },
            tooltip: {},
            legend: {
                data:['销量']
            },
            grid: {
              left: "0",
              
            },
            xAxis: {
                data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子","衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子","衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'line',
                data: [20, 40, 36, 10, 10, 20,5, 20, 36, 10, 10, 20,5, 20, 36, 10, 10, 20]
            }]
        };

        // 使用刚指定的配置项和数据显示图表。
        lmyChart.setOption(leftOption);
        rmyChart.setOption(rightOption);
  },
}

</script>
<style lang='scss' scoped>
.box{
    width: 2420px;
    height: 500px;
    position: relative
}
#left,#right{
    width: 1200px;
    height: 1000px;
    position: absolute;    
}
#left{
    top:0;
    left:0
}
#right{
    top:0;
    left:1140px
}
</style>